import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import './assets/global_assets/js/main/jquery.min.js';
import './assets/global_assets/js/main/bootstrap.bundle.min.js';
import './assets/global_assets/js/plugins/loaders/blockui.min.js';
import './assets/global_assets/js/plugins/ui/ripple.min.js';
import './assets/assets/js/app.js';

import ApiService from './services/api.service'
import TokenService from './services/storage.service'

Vue.config.productionTip = false

ApiService.init(process.env.VUE_APP_URL)

if (TokenService.getToken()) ApiService.setHeader()

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
